import React, { useState } from 'react';

const Calendar = (props) => {
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  const months = Array.from({ length: 12 }, (_, month) => month);
  const dropdownValueDetails = props.props.cropDataSliced.find((crop) => { if (crop.name == props.props.dropdownValue.name) { return true } })
  const renderMonth = (month) => {
    const firstDay = new Date(selectedYear, month, 1).getDay();
    const daysInMonth = new Date(selectedYear, month + 1, 0).getDate();

    const isInPlanting = (day, month) => {
      let plantingSeason = dropdownValueDetails.plantingSeasons

      if (plantingSeason.start !== undefined && plantingSeason.ends !== undefined) {
        if ((plantingSeason.start.slice(0, 2) == day + 1 && plantingSeason.start.slice(3) == month + 1)) {
          return "first"
        } else if (plantingSeason.ends.slice(0, 2) == day && plantingSeason.ends.slice(3) == month + 1) {
          return "last"
        } else if ((plantingSeason.start.slice(3) < month + 1) && (plantingSeason.ends.slice(3) > month + 1)) {
          return true
        } else if (plantingSeason.start.slice(0, 2) < day + 1 && plantingSeason.start.slice(3) == month + 1) {
          return true
        } else if (plantingSeason.ends.slice(0, 2) > day && plantingSeason.ends.slice(3) == month + 1) {
          return true
        } else {
          return false
        }
      } else {
        return
      }
    }

    const isInHarvest = (day, month) => {
      let harvest = dropdownValueDetails.harvest
      if (harvest !== undefined) {
        if ((harvest.start.slice(0, 2) == day + 1 && harvest.start.slice(3) == month + 1)) {
          return "first"
        } else if (harvest.ends.slice(0, 2) == day && harvest.ends.slice(3) == month + 1) {
          return "last"
        } else if ((harvest.start.slice(3) < month + 1) && (harvest.ends.slice(3) > month + 1)) {
          return true
        } else if (harvest.start.slice(0, 2) < day + 1 && harvest.start.slice(3) == month + 1) {
          return true
        } else if (harvest.ends.slice(0, 2) > day && harvest.ends.slice(3) == month + 1) {
          return true
        } else {
          return false
        }
      }
    }

    const d = new Date();
    let currentMonth = d.getMonth();
    let currentDay = d.getDate()

    return (
      <div key={month} className="yearly-calendar-month" style={month == currentMonth ? {
        background: "rgb(142, 168, 81, 0.5)"
      } : {}}>
        <h3 style={month == currentMonth ? {
          fontWeight: "600"
        } : {}}>
          {new Date(selectedYear, month).toLocaleDateString('en-US', { month: 'long' })}
        </h3>
        <div className="yearly-calendar-grid">
          <div className="yearly-calendar-dayName">S</div>
          <div className="yearly-calendar-dayName">M</div>
          <div className="yearly-calendar-dayName">T</div>
          <div className="yearly-calendar-dayName">W</div>
          <div className="yearly-calendar-dayName">T</div>
          <div className="yearly-calendar-dayName">F</div>
          <div className="yearly-calendar-dayName">S</div>
          {[...Array(firstDay)].map((_, index) =>
            <div
              key={`empty-${index}`}
              className="yearly-calendar-day empty">
            </div>
          )}
          {Array.from({ length: daysInMonth }, (_, day) => day + 1).map((day) => {
            const inPlanting = isInPlanting(day, month)
            const inHarvest = isInHarvest(day, month)

            const className = () => {
              if (inPlanting == true) {
                return "yearly-calendar-day inSeasonPlanting"
              } else if (inPlanting == "first") {
                return "yearly-calendar-day inSeasonPlanting inSeasonFirst"
              } else if (inPlanting == "last") {
                return "yearly-calendar-day inSeasonPlanting inSeasonLast"
              } else if (inHarvest == true) {
                return "yearly-calendar-day inSeasonHarvest"
              } else if (inHarvest == "first") {
                return "yearly-calendar-day inSeasonHarvest inSeasonFirst"
              } else if (inHarvest == "last") {
                return "yearly-calendar-day inSeasonHarvest inSeasonLast"
              } else {
                return "yearly-calendar-day"
              }
            }

            let title;
            if (currentDay == day && currentMonth == month) {
              title = "Today"
            } else if (inPlanting == true) {
              title = "Planting"
            } else if (inHarvest == true) {
              title = "Harvest"
            } else {

            }

            return <div
              key={`day-${day}`}
              title={title}
              style={(currentDay == day && currentMonth == month) ? {
                background: "rgb(142, 168, 81, 0.5)"
              } : {}}
              className={className()}>
              {day}
            </div>
          })}
        </div>
      </div>
    );
  };

  return (
    <div className="yearly-calendar">
      <div className="yearly-calendar-container">
        <span>
          <span>
            <div style={{
              width: "10px",
              height: "10px",
              backgroundColor: "rgb(49, 67, 60)"
            }} />
            Planting: {dropdownValueDetails.plantingSeasons.start} - {dropdownValueDetails.plantingSeasons.ends} 
          </span>
          <span>
            <div style={{
              width: "10px",
              height: "10px",
              backgroundColor: "rgb(49, 67, 60)"
            }} />
            Harvest: {dropdownValueDetails.harvest.start} - {dropdownValueDetails.harvest.ends}
          </span>
        </span>
        {months.map((month) => renderMonth(month))}
      </div>
    </div>
  );
};

export default Calendar;