import React, { useEffect, useState } from "react";
import Calendar from "../components/Calender";
import {Link} from "react-router-dom"

const importAll = (require) => {
    return require.keys().map(require)
}

const GrowingGuide = () => {
    const cropData = importAll(require.context('../../public/assets/plants', false, /\.(json)$/))
    const cropDataSliced = cropData.slice(0, 18).concat(cropData.slice(19))
    const cropsIndex = cropData.slice(18, 19)[0].plants
    const [dropdownValue, setDropdownValue] = useState(cropsIndex[0])
    const [dropdownValueDetails, setDropdownValueDetail] = useState(() => {
        return cropDataSliced.find((data) => data.name == dropdownValue.name)
    })

    const JsonToTable = (data) => {
        return data !== undefined && (
            <table className="harvestHelper">
                <tbody>
                    <tr>
                        <th>Category</th>
                        <th>Edible Parts</th>
                        <th>Hardiness</th>
                        <th>Harvest</th>
                    </tr>
                    <tr>
                        <td>{data.data.cultivationCategory}</td>
                        <td>{data.data.edibleParts.join(', ')}</td>
                        <td>{`${data.data.hardinessZone.min} - ${data.data.hardinessZone.max}`}</td>
                        <td>{data.data.harvest.duration.min}-{data.data.harvest.duration.max}{data.data.harvest.duration.unit}</td>
                    </tr>
                    <tr>
                        <th>Soil Impact</th>
                        <th>Species</th>
                        <th>Sun</th>
                        <th>Yield</th>
                    </tr>
                    <tr>
                        <td>{data.data.soilImpact}</td>
                        <td>{data.data.species}</td>
                        <td>{data.data.sun.min}-{data.data.sun.max}</td>
                        <td>{data.data.yield.value} gr*mÂ²</td>
                    </tr>
                </tbody>
            </table>
        );
    };

    const img = require(`../../public/assets/plants/${dropdownValue.name}.jpg`)

    useEffect(()=>{
        document.getElementById("dropdown").focus()
    },[])

    return (<>
        <div className="vegetablePage">
            <h3>Growing Guide</h3>
            <p>This secton serves suggestions on how you could organize a little plot of your own.</p>
            <span className="harvestHelper">
                <span>
                <select
                    id="dropdown"
                    value={dropdownValue.name.slice(0, 1).toUpperCase() + dropdownValue.name.slice(1)}
                    onChange={(e) => {
                        setDropdownValue({ imgUrl: `/${e.target.value.toLowerCase()}.jpg`, name: e.target.value.toLowerCase() })
                        setDropdownValueDetail(cropDataSliced.find((data) => data.name == e.target.value.toLowerCase()))
                    }}>
                    {cropsIndex.map((crop, index) => {
                        return <option key={index}>
                            {crop.name.slice(0, 1).toUpperCase() + crop.name.slice(1)}
                        </option>
                    })}
                </select>
                <span>
                    <span>
                        <img src={img && img} />
                    </span>
                        {dropdownValueDetails.description}
                        <Link to={dropdownValueDetails.wiki}>{dropdownValueDetails.wiki}</Link>
                    </span>
                </span>
                <span>
                    <JsonToTable data={cropDataSliced.find((crop) => { if (crop.name == dropdownValue.name) { return true } })} />
                    <Calendar props={{ dropdownValue: dropdownValue, cropsIndex: cropsIndex, cropDataSliced: cropDataSliced }} />
                </span>
            </span>
        </div>
    </>)
}

export default GrowingGuide